import React, { useState } from 'react'
import './index.css';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import ToDo from './ToDo' 
import { Redirect, useHistory } from 'react-router-dom'


const Main = () => {
    // const history = useHistory();
    // const navigateTo = () => {history.push('ToDo')};
    const [isAuth,setIsAuth]=useState(true);
    if(!isAuth){
        return <Redirect to="/ToDo"/>
    }
    return (
        <div class="container" style={{display:"flex",justifyContent:"center",alignItems:"center",marginTop:"4rem"}}>
                <div class="row" style={{width:"30rem"}} >
                    <div className="col-sm" >
                     
                        <div class="card" style={{height:"20rem"}}>
                            <h5 class="card-header" style={{textAlign:"center",fontSize:"3rem",color:" #8566aa",fontWeight:"bolder",  textShadow: "5px 5px 5px rgba(0,0,0,0.3)"}}>WELCOME</h5>
                            <div class="card-body" style={{backgroundColor:"white",flex:"0.8 1 auto",textAlign:"center"}} >
                                <br/>
                                <br/>
                            <div class="row g-3" style={{backgroundColor:"white"}}>
                                <div class="col" style={{backgroundColor:"white"}}>
                                <button class="btn btn-primary" onClick={()=>setIsAuth(false)} style={{fontSize:"2rem"}}>Continue</button>
                                    {/* <input type="text" className="input" placeholder="Username" aria-label="Name" /> */}
                                </div>
                            </div>
                            
            
                                {/* <a href={ToDo} class="btn btn-primary">Go somewhere</a> */}
                            </div>
                        </div>
                    </div>
                </div>
        </div>
    )
}

export default Main
