import {React,useState} from 'react'
import AddOutlinedIcon from '@material-ui/icons/AddOutlined';
import {connect} from 'react-redux'
import {addList} from '../redux/dataadd/addAction';


const Addtodo = (props) =>{
    return(
        <div>
            <form onSubmit={(event)=>{
                //data add 
                event.preventDefault()
                let input = event.target.userInput.value;
                if (input !== '') {
                    props.dispatch(addList(input))
                    event.target.userInput.value="";
                }
            }} style={{backgroundColor:"white"}}>
            <input type="text" placeholder="Add a Items"  name='userInput' id="userInput" /> 
            
            <button className="but"><AddOutlinedIcon className="add"/></button> 
            </form>
        </div>
    )
}
export default connect()(Addtodo)