import React from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import Addtodo from './component/Addtodo'
import List from './component/List'

const App1 = () =>{
    return(
        <>
            <div class="container" style={{display:"flex",justifyContent:"center",alignItems:"center",marginTop:"4rem"}}>
                <div class="row" style={{width:"30rem"}} >
                    <div className="col-sm" >
                        <div class="card" style={{backgroundColor:"white"}} >
                            <br/>
                            <h1 style={{fontSize:"3rem"}} >ToDo List</h1>
                            <div class="card-body" style={{backgroundColor:"white",textAlign:"center",marginBottom:"1rem"}} >
                                <br/>
                                <Addtodo/>
                                <br/>
                                <List/>
                            </div>
                        </div>
                    </div>
                </div>
                <br/>
                <br/>
            </div>
        </>
    );
};
export default App1;