import {ADD_LIST,DELETE_LIST} from './addType'

const initialState ={
    data:[],
}

const addReducer = (state=initialState,action) =>{
    switch(action.type){
        case ADD_LIST:
            // array
            return{
                ...state,
                data:[
                    ...state.data,
                    {
                        message:action.message,
                        id:action.id,
                    },
                ]
            }
        case DELETE_LIST:
            //delete message by filter 
            const todos = state.data.filter((todo)=> todo.id !==action.id)
                return {
                    ...state,
                    data:todos, 
                }  
        default: return state
    }
}

export default addReducer